@import "mixin/baseImport";
@import "_sprite/club_color_magic_sprite";

//#wrap{
//	&.club-color-magic{
		.wrap-outer{
			.wrap-inner{background: url("/assets/images/club_color_magic/bg_club_color_magic01.jpg") no-repeat 0 0;}
		}
		//content
		#container{}
		#content{}
		.content-wrap{
			&__outer{}
			&__inner{}
			.swiper{padding: 110px 0 0;height: 768px;position: relative;box-sizing: border-box;
				&-container{height: 544px;}
				&-wrapper{}
				&-controller{
					.btn-swiper-prev{position: absolute;top: 325px;left: 15px;width: 44px;height: 72px;background: url("/assets/images/club_color_magic/btn_prev01.png") no-repeat 0 0;opacity: 1;transition: opacity 0.3s;z-index: 3;
						&.swiper-button-disabled{opacity: 0;}
					}
					.btn-swiper-next{position: absolute;top: 325px;right: 15px;width: 44px;height: 72px;background: url("/assets/images/club_color_magic/btn_next01.png") no-repeat 0 0;opacity: 1;transition: opacity 0.3s;z-index: 3;
						&.swiper-button-disabled{opacity: 0;}
					}
				}
				&-slide{padding-top: 0;z-index: 1;}
			}
			.clubhouse-color-magic{
				&__area{}
				&__box{}
				&__bg{}
				&__items{width: 826px;height: 471px;margin: 0 auto;display: flex;align-items: center;justify-content: center;flex-wrap: wrap;
					&.length1{
						.clubhouse-color-magic__item{
							&:nth-of-type(1){margin: 32px 40px 0;}
							&:nth-of-type(2){display: none;}
							&:nth-of-type(3){display: none;}
							&:nth-of-type(4){display: none;}
							&:nth-of-type(5){display: none;}
						}
					}
					&.length2{
						.clubhouse-color-magic__item{
							&:nth-of-type(1){margin: 32px 20px 0;}
							&:nth-of-type(2){margin: 32px 20px 0;}
							&:nth-of-type(3){display: none;}
							&:nth-of-type(4){display: none;}
							&:nth-of-type(5){display: none;}
						}
					}
					&.length3{
						.clubhouse-color-magic__item{
							&:nth-of-type(1){margin: 32px 10px 0;}
							&:nth-of-type(2){margin: 32px 10px 0;}
							&:nth-of-type(3){margin: 32px 10px 0;}
							&:nth-of-type(4){display: none;}
							&:nth-of-type(5){display: none;}
						}
					}
					&.length4{
						.clubhouse-color-magic__item{
							&:nth-of-type(1){margin: 32px 20px 0;}
							&:nth-of-type(2){margin: 32px 20px 0;}
							&:nth-of-type(3){margin: 40px 20px 0;}
							&:nth-of-type(4){margin: 40px 20px 0;}
							&:nth-of-type(5){display: none;}
						}
					}
					&.length5{
						.clubhouse-color-magic__item{
							&:nth-of-type(1){margin: 32px 10px 0;}
							&:nth-of-type(2){margin: 32px 10px 0;}
							&:nth-of-type(3){margin: 32px 10px 0;}
							&:nth-of-type(4){margin: 40px 40px 0;}
							&:nth-of-type(5){margin: 40px 0 0;}
						}
					}
				}
				&__item{position: relative;width: 240px;height: 234px;display: block;transform: scale(1);transition: transform 0.3s;
					@for $i from 1 through 5{
						&.bg-color0#{$i}{
							background: url("/assets/images/club_color_magic/bg_item0#{$i}.png") no-repeat 0 0;
							.btn{display: block;width: 100%;height: 100%;}
							.img-box{
								background: url("/assets/images/club_color_magic/bg_item0#{$i}_default.png") no-repeat 0 0;
							}

							&.disabled{
								.img-box{
									background: url("/assets/images/club_color_magic/bg_item0#{$i}_disabled.png") no-repeat 0 0;
								}
							}

							&.complete{
								.complete-box{
									&:before{content: "";position: absolute;top: -6px;left: 96px;width: 52px;height: 47px;background: url("/assets/images/club_color_magic/icon_complete.png") no-repeat 0 0;}
								}
							}
						}
					}
					&.active{
						&:not(.disabled):not(.vacation){transform: scale(1.05);}
					}
					&.default{
					}
					&.disabled{
						.tit-week{opacity: 0.2;color: #222;}
					}
					&.complete{
						.complete-box{
							display: block;position: absolute;top: -19px;left: 0;width: 100%;height: 47px;text-align: center;
						}
					}
					&.this-week{
						.btn:before{content: "";position: absolute;top: -40px;left: 0;width: 290px;height: 238px;animation: this-week-icon 1s steps(2) infinite forwards;background: url("/assets/images/sprite/club_house_color_magic_this_week.png") no-repeat 0 0;z-index: 1; }
						.tit-week{color: #fff509;}
					}
					&.vacation{
						background: url("/assets/images/club_color_magic/bg_item_vacation.png") no-repeat 0 0 !important;
						.img-box{display: none;}
						.tit-week{opacity: 0.2;color: #222;}
					}
					.img-box{
						position: absolute;
						bottom: 55px;
						left: 30px;
						width: 184px;
						height: 173px;
					}
					.complete-box{display: none;}
					.tit-week{
						position: absolute;bottom: 11px;left: 0;width: 100%;height: 30px;display: flex;font-family: $nsroundecp;font-size: 30px;color: #fff;letter-spacing: 1px;align-content: center;justify-content: center;
						img{vertical-align: top;}
						.number-box{margin: 3px 0 0 10px;}
					}
					.img{position: absolute;bottom: 39px;left: 34px;overflow: hidden;width: 115px;height: 115px;}
				}
			}
		}
	//}
//}

